//meta-web.scss
/*
 * meta-web.css auto-generated by sass from ../meta-web.scss
 */

// variables
$font: 'Neuropol';

// mixins
@mixin viewport { 
  left: 0vw;
  top: 0vh;
  width: 100vw;
  height: 100vh;
}



/*
 * document frame 
 */

/*
 * body is at default z-index: 0, level = -INF 
 * w:100vw h:100vh 
 */
@font-face {
  font-family: $font;
  src: url(../fonts/neuropol.ttf); 
}
body {
  @include viewport;
  position: relative;
  overflow: hidden;
  background-color: #ddddff;
  z-index: 0; 
}



/* 
 * mediaweb layers - 4
 */

/*
 * HTML space - layer=html dimension=0 z-index: 0.1, level = -1
 * w:100vw h:100vh 
 * flat 'base' for background tables, trees etc. for application such as
 * metaforms composition and editing.
 */
.mediaweb .base {
  @include viewport;
  display: block;
  position: absolute;
  background-color: transparent;
  z-index: 0.1;
}


/*
 * webGL space - layer=BG dimension=3 z-index: 1, level = 0
 * w:100vw h:100vh 
 * 3D vector space with z-axis passing through center of svg stage
 * uses right-hand coordinate system so positive z-axis points toward the
 * user-camera, positive y-axis points up, positive x-axis points right
 */
.mediaweb .space {
  @include viewport;
  display: block;
  position: fixed;
  pointer-events: none;
  background-color: transparent;
  z-index: 1;
}


/*
 * svg stage - layer=near-clip-plane dimension=2 z-index: 10, level = 1
 * w:100vw h:100vh 
 * 2D vector space with viewBox 100x100 vector space coordinates
 * set with upper-left corner at (-50,-50) so stage-center is at (0,0)
 * thenrte space also corresponds to 100vw x 100vh and 100% x 100% units
 * uses standard 2D graphics corrdinate system so positive-x points right
 * and positive-y points down (nb!)
 * svg uses preserveAspectRatio = 'none' so despite the viewport
 * size or aspect ratio the vector space is homeomorphic and never clipped
 */
.mediaweb .stage {
  @include viewport;
  display: block;
  position: fixed;
  pointer-events: none; 
  z-index: 10;
}


/*
 * html UI - layer=overlay dimension=1 z-index: 100, level = 2
 * controls associated with scenes and navigation
 */
.mediaweb .ui {
  position: absolute;
  z-index: 100;
}
/* text */
.mediaweb h1, h2, h3, h5, h6 { 
  font-family: $font;
  text-shadow: 0 -2px 3px #aaaaaa, 0 -4px 3px #666666;
}
.mediaweb h4 { 
  font-family: $font;
  text-shadow: 0 -1px 2px #aaaaaa, 0 -2px 2px #666666;
}
/* links */
.mediaweb a {
  font-family: $font;
  text-decoration: none;
  color: #fff;
}
.mediaweb a:hover, a:active {
  color: #9685BA; 
}

